import { Dictionary, Guid } from "@logofx/aurelia-mvvm-plugin";
import { BaselineDto } from "data/dto";
import { IDataProvider } from "data/providers/contracts/data-provider";

function randomDate(start, end) {
  return new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()));
}

export class DataProvider implements IDataProvider {
  
  private _dbBaselines: Dictionary<string, BaselineDto> = new Dictionary<string, BaselineDto>();

  constructor() {
    Array.from({ length: 50 }, (x, i) => {
      return {
        id: Guid.create().toString(),
        name: `Baseline ${i}`,
        originalFileNames: [`Baseline ${i}_1.json`, `Baseline ${i}_2.json`, `Baseline ${i}_3.json`],
        singleCellCapacity: i,
        topologyOfCellPack: {
          pValue: i,
          sValue: i
        },
        uploadedAt: randomDate(new Date(2012, 0, 1), new Date()).toISOString()// new Date().toISOString()
      };
    }).forEach(x => this._dbBaselines.setValue(x.id, x));
  }

  public async getBaselinesAsync(): Promise<BaselineDto[]> {    
    return new Promise<BaselineDto[]>(resolve => resolve(this._dbBaselines.values()));
  }

  public async putBaselineAsync(dto: BaselineDto): Promise<void> {
    if (!this._dbBaselines.containsKey(dto.id)) {
      throw new Error(`Can't find baseline with id: ${dto.id}`);
    }

    this._dbBaselines[dto.id] = dto;
  }

  public async postBaselineAsync(dto: BaselineDto): Promise<void> {
    dto.uploadedAt = new Date().toISOString();
    this._dbBaselines.setValue(dto.id, dto);
  }
  
  public async deleteBaselineAsync(id: string): Promise<void> {
    if (!this._dbBaselines.containsKey(id)) {
      throw new Error(`Can't find baseline with id: ${id}`);
    }

    this._dbBaselines.remove(id);
  }
}
